<template>
  <div class="persion">
    <div class="popup" v-show="hidepopup">
      <img src="https://m.jyacg.com/static/img/pig.59633eb.png" />
      <h4>是否将所选移除书架</h4>
      <div>
        <button @click="removes(false)">考虑一下</button
        ><button @click="removes(true)">残忍移除</button>
      </div>
    </div>
    <div class="persion-s">
      <div class="persion-s-notice">
        <van-notice-bar
          left-icon="data:image/gif;base64,"
          :scrollable="false"
          background="#fff"
        >
          <van-swipe
            vertical
            class="notice-swipe"
            :autoplay="3000"
            :show-indicators="false"
          >
            <van-swipe-item v-for="item of getAnnounceList" :key="item.id"
              ><a :href="item.url" target="_blank">{{
                item.text
              }}</a></van-swipe-item
            >
          </van-swipe>
        </van-notice-bar>
      </div>
      <div class="persion-s-Info">
        <router-link to="/mineself">
          <img :src="getavatar" alt="" />
          <span>{{  getauthor_name }}</span>
       </router-link>
        <p @click="manage = !manage">管理书架</p>
      </div>
    </div>
    <div class="persion-x">
      <div
        v-show="bookslist.length"
        class="bookslist"
        v-for="item in bookslist"
        :key="item.id"
      >
        <img
          src="@/assets/img/sc.png"
          class="removes"
          v-show="manage"
          @click="popup(item.id)"
        />
        <img :src="item.cover_image" @click="$bus.$emit('details', item.id)"/>
        <div>
          {{ item.name }}
        </div>
      </div>

      <div v-show="bookslist.length" class="bookslist boxadd">
        <router-link to="/index">+</router-link>
      </div>

      <div
        v-show="bookslist.length"
        class="bookslist"
        v-for="item in bookslistlength"
        :key="item"
      ></div>

      <div v-show="!bookslist.length" class="wu">暂无书籍~</div>
    </div>
  </div>
</template>
  
  <script>
export default {
  name: "Mypersion",
  components: {},
  data() {
    return {
      bookslist: [],
      AnnounceList: [],
      manage: false,
      hidepopup: false,
      userapp:JSON.parse(localStorage.getItem("userapp"))
    };
  },
  methods: {
    popup(id) {
      this.hidepopup = id;
    },
    removes(value){
      if (value) {
        this.axios.post(
          "https://jyapi.jyacg.com/admin/user/books_delete",
          { string: this.hidepopup },
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        ).then(res=>{
          console.log(res);
          this.getmybook();
        })
      }
      this.hidepopup = false;
    },
    getmybook() {
      this.axios
        .get("https://jyapi.jyacg.com/admin/user/mybooks", {
          headers: {
            authorization: localStorage.getItem("api_token"),
          },
        })
        .then((res) => {
          if (res.status == 200) {
            if (res.data.code == 0) {
              this.bookslist = res.data.data.data;
            }
          }
        });
    },
  },
  computed: {
    getavatar(){
      
      return this.userapp ?this.userapp.avatar :''
    },
    getauthor_name(){
 
      return this.userapp ? this.userapp.author_name :''
    },
    getAnnounceList() {
      return this.AnnounceList.splice(0, 3);
    },
    bookslistlength() {
      return 2 - (this.bookslist.length % 3);
    },
  },
  beforeRouteEnter(to, from, next) {
    if (localStorage.getItem("api_token")) {
      next();
    } else {
      next((vm) => {
        vm.$router.replace({
          name: "login",
        });
      });
    }
  },
  mounted() {
    this.$bus.$emit("active", 3);
    this.axios
      .get("https://jyapi.jyacg.com/admin/user/moneyinfo", {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then((res) => {
        if (res.status == 200) {
          if (res.data.code == 0) {
            localStorage.setItem("userapp", JSON.stringify(res.data.data));
            this.userapp = res.data.data
          } else if (res.data.code == 1002) {
            if (res.data.msg == "账号验证失效，请重新登录") {
              localStorage.removeItem("api_token");
              this.$dialog({
                title: "温馨提示",
                message: res.data.msg,
              }).then(() => {
                this.$router.replace({
                  name: "login",
                });
              });
            }
          }
        }
      });

    this.getmybook();
    this.axios
      .get("https://jyapi.jyacg.com/admin/user/personcenter", {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then((res) => {
        if (res.status == 200) {
          if (res.data.code == 0) {
            this.AnnounceList = res.data.data.notices;
          }
        }
      });
  },
};
</script>
<style lang="scss">
.notice-swipe {
  height: 48px;
  line-height: 48px;
}
.van-notice-bar {
  a {
    color: #000;
  }
  font-size: 16px;
  height: 48px;
  line-height: 48px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}
.van-icon {
  margin-right: 15px;
  width: 30px;
  height: 30px;
}
.persion {
  width: 350px;
  height: 555px;
  margin: 0px 12.5px;
  .popup {
    position: fixed;
    top: 200px;
    left: 75px;
    height: 250px;
    width: 230px;
    background-color: #fff;
    text-align: center;
    z-index: 999;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    border-radius: 25px;
    img {
      margin-top: 25px;
      width: 120px;
    }
    h4 {
      margin-top: 30px;
      font-size: 14px;
      font-weight: 600;
      color: #333;
    }
    div {
      margin-top: 25px;
      button {
        border-radius: 20px;
        width: 86px;
        height: 35px;
        padding: 0px;
        border: none;
        margin: 0 14px;
        background-color: #439fff;
        color: #fff;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
      }
    }
  }
  .persion-s,
  .persion-x {
    width: 350px;
    min-height: 121px;
    margin-top: 14px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  }
  .persion-s {
    width: 330px;
    min-height: 99px;
    padding: 10px 11px;
    .persion-s-notice {
      img {
        width: 30px;
        height: 30px;
      }
      width: 328px;
      height: 48px;
    }
    .persion-s-Info {
      margin-top: 11px;
      width: 328px;
      height: 45px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      a{
        height: 44px;
        display: flex;
        align-items: center;
        img {
          width: 44px;
          height: 44px;
          margin-right: 20px;
          border-radius: 50%;
        }
        span {
          margin-left: 25px;
          color: #666;
          font-size: 15px;
          font-weight: 500;
        }
      }
      p {
        font-weight: 900;

        margin-left: 25px;
        color: #666;
        font-size: 15px;
      }
    }
  }

  .persion-x {
    width: 320px;
    padding: 15px;
    padding-bottom: 0px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    .wu {
      height: 192px;
      text-align: center;
      line-height: 192px;
      font-weight: bold;
      font-size: 17px;
      color: #999;
    }
    .boxadd {
      text-align: center;
      a {
        border: 1px dotted #999;
        line-height: 100px;
        display: block;
        width: 100%;
        color: #999;
        height: 100px;
        font-size: 40px;
      }
    }
    .bookslist {
      width: 72.5px;
      height: 127px;
      margin: 0 5px;
      margin-bottom: 14px;
      position: relative;
      img {
        height: 100px;
        width: 72.5px;
      }
      .removes {
        position: absolute;
        width: 18px;
        height: 18px;
        top: -9px;
        right: -9px;
      }
      div {
        font-size: 14px;
        color: #333;
        margin-top: 10px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 72.5px;
      }
    }
  }
}

.van-dialog {
  border-radius: 5px;
}
.van-dialog__header {
  padding-top: 15px;
  font-weight: 600;
  font-size: 18px;
}
.van-dialog {
  width: 300px;
}
.van-dialog__message {
  margin-top: 20px;
  font-size: 16px;
  color: #000;
  border-bottom: 1px solid #ddd;
}
.van-button__text {
  color: #000;
}
</style>